.messages {
  padding: 16px;
  overflow: auto;
  flex: auto;
  background: rgba(0, 0, 0, 0.1);
}

.messages::-webkit-scrollbar {
  width: 6px;
}

.messages::-webkit-scrollbar-track {
  background: transparent;
}

.messages::-webkit-scrollbar-thumb {
  background: rgba(255, 215, 0, 0.3);
  border-radius: 3px;
}
