.messageBox {
  border-radius: 16px;
  padding: 10px 16px;
  display: inline-block;
  max-width: 80%;
}

.messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 0.95rem;
  word-wrap: break-word;
  line-height: 1.4;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 6px;
}

.sentText {
  display: flex;
  align-items: center;
  color: rgba(255, 255, 255, 0.5);
  letter-spacing: 0.3px;
  font-size: 0.8rem;
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}

.colorWhite {
  color: white;
}

.colorDark {
  color: rgba(255, 255, 255, 0.9);
}

.backgroundBlue {
  background: linear-gradient(135deg, rgba(255, 215, 0, 0.3) 0%, rgba(255, 170, 0, 0.3) 100%);
  border: 1px solid rgba(255, 215, 0, 0.2);
}

.backgroundLight {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.1);
}
